const stringMarkup = images.reduce((totalString, elem) => {
  return (
    totalString +
    `<li class="gallery-item">
                <a class="gallery-link" href="${elem.original}"> 
                    <img
                    class="gallery-image"
                    src="${elem.preview}"
                    alt="${elem.description}"
                    />
                </a>
            </li>`
  );
}, '');

const list = document.querySelector('.gallery');

list.innerHTML = stringMarkup;

const gallery = new SimpleLightbox('.gallery a', {
  navText: ['<', '>'],
  captionsData: 'alt',
  captionDelay: '250',
});
